<template>
  <div class="sensor first">
    <h1 class="ui dividing header">Sensor Monitoring</h1>
    <div class="ui two column grid segment">
      <div class="column">
          <div class="ui checkbox"><input type="checkbox" value="1" v-model="filtered[0]" id="s1"><label for="s1">Hujan</label></div><br>
          <div class="ui checkbox"><input type="checkbox" value="2" v-model="filtered[1]" id="s2"><label for="s2">TMA Sungai</label></div><br>
          <div class="ui checkbox"><input type="checkbox" value="3" v-model="filtered[2]" id="s3"><label for="s3">TMA Bendungan</label></div>
          </div>
      <div class="column ui input"><input type="text" name="device_id" placeholder="1710-1">
      </div>
    </div>
    <div class="ui horizontal divider">Logger ({{ sensors.length }})</div>
    <div class="ui cards">
    <div v-for="logger in sensors" :key="logger.id" v-bind:class="logger.pos.color" @click="displayDetails(logger.id)" class="card">
      <div class="content">
        <div class="ui header" v-bind:class="logger.pos.color">{{ (logger.pos !== undefined) ? logger.pos.cname : logger.id }}<span class="right floated meta">{{ logger.sampling|moment("HH:mm")}}</span></div>
        <div class="meta"><span>{{ (logger.pos !== undefined) ? logger.id : "" }}</span>
          <span class="right floated">On {{ logger.up_since | moment("from") }}</span>
        </div>
        <div class="footer">
          <span><i class="thermometer half icon"></i>{{ logger.temperature.toFixed() }}&#8451;</span>
          <span>&bullet; {{ logger.humidity.toFixed() }}%</span>
          <span class="right floated"><i class="signal icon"></i> {{ logger.signal_quality }}/30</span>
        </div>
      </div>
    </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  name: 'SensorWorld',
  methods: {
    displayDetails (device) {
      this.$router.push({name: 'SensorShow',
        params: {id: device},
        pos: this.sensors[0]})
    }
  },
  data () {
    return {
      filtered: [1, 0, 1],
      sensors: this.sensors
    }
  },
  created () {
    let self = this
    this.sensors = []
    self.loggers = []
    axios.get('//hidrologi.bbws-bsolo.net/api/logger')
      .then(response => {
        self.loggers = response.data
        console.log(this.loggers)
      })
    this.ws = new WebSocket('ws://mqtt.bbws-bsolo.net:22286')
    this.ws.onopen = function (ws) {
      console.log('connected')
    }
    this.ws.onmessage = function (msg) {
      let data = JSON.parse(msg.data)
      if (data.device !== undefined) {
        let device = data.device.split('/')[1]
        data['id'] = device
        let pos = self.loggers.filter(pos => (pos.prima_id === device))[0]
        if (pos !== undefined) {
          let warna = ['green', 'blue', 'red', 'orange'][pos.type]
          pos.color = warna
          data.pos = pos
        } else {
          pos.color = 'gray'
          data.pos = pos
        }
        console.log(data)
        let foundIndex = self.sensors.findIndex(elem => elem.id === device)
        if (foundIndex !== -1) {
          self.sensors.splice(foundIndex, 1, data)
        } else {
          self.sensors.push(data)
        }
      }
    }
    this.ws.onclose = function (ws) { console.log(ws) }
  },
  destroyed () {
    this.ws.close()
    // this.$parent.showCloud = false
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.first { margin-top: 5em; }
</style>



