<template>
  <div class="sensor-single first">
    <div class="ui dividing header"><router-link to="/" class="level up alternate icon">&larr;</router-link> {{ device }}</div>
    <div class="ui two column grid">
      <div class="column">
      <highcharts :options="options" ref="bagan"></highcharts>
      </div>
      <div class="column">2</div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'SensorShow',
  data () {
    return {
      device: this.device,
      pos: this.pos,
      options: {title: {text: 'TMA'},
        series: [
          {name: 'Suhu', data: []},
          {name: 'Kelembaban', data: []},
          {name: 'Signal', data: []},
          {name: 'Batt', data: []}
        ]}
    }
  },
  created () {
    this.device = this.$route.path.slice(1)
    axios.get('//hidrologi.bbws-bsolo.net/api/logger')
      .then(response => {
        self.loggers = response.data
        this.pos = self.loggers.filter(pos => (pos.prima_id === this.device))[0]
        if (this.pos !== undefined) {
          let warna = ['green', 'blue', 'red', 'orange'][this.pos.type]
          this.pos.color = warna
        }
      })
    let refs = this.$refs
    axios.get('//hidrologi.bbws-bsolo.net/api/sensor/' + this.device)
      .then(response => {
        let temp = response.data.periodic.map(x => x.temperature)
        let kelembaban = response.data.periodic.map(x => x.humidity)
        let sq = response.data.periodic.map(x => x.signal_quality)
        let bat = response.data.periodic.map(x => x.battery)
        let chart = refs.bagan
        // console.log(refs.bagan)
        chart.chart.series[0].setData(temp, true)
        chart.chart.series[1].setData(kelembaban, true)
        chart.chart.series[2].setData(sq, true)
        chart.chart.series[3].setData(bat, true)
      })
  }
}
</script>
<style scoped>
.first { margin-top: 5em; }
</style>



